* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #17153B    ;
    color: white;
}

.container {
    margin: 1rem;
    text-align: center;
}

.container h2{
    font-size: 3rem;
}

.nav {
    background-color: #2E236C    ;
    color: rgb(255, 255, 255);
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    padding: 0 1rem;
}

.site-title {
    font-size: 4.5rem;
}

.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
    font-size: 1.5rem;
}

.nav a {
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
}

.nav li.active {
    background-color: #8559ec;
}

.nav li:hover {
    background-color: #8559ec;
}

